import {T, useTNavigate} from "../translation";
import React, {useState} from "react";
import "./Step3.css";

export default function Step3() {
    const [active, setActive] = useState(1)
    const navigate = useTNavigate()

    const activate = function (n: number) {
        return () => {
            if (n !== active) {
                return
            }
            if (n === 5) {
                navigate("/mouse/4")
                return
            }
            setActive(n+1)
        }
    }

    return <div className={"step3"}>
        <div className={"boxcenter"} style={{marginTop: "1.5rem"}}><div className={"box"}>
            <div className={"text text--center large"}>
                <T en={"Move the mouse cursor to red points!"} de={"Bewege deinen Mauszeiger zu jedem roten Punkt!"} />
            </div>
        </div></div>
        <div className={"step3__point step3__point--1" + (active === 1?" step3__point--active":(active > 1?" step3__point--done":""))} onMouseOver={activate(1)}>
            1
        </div>
        <div className={"step3__point step3__point--2" + (active === 2?" step3__point--active":(active > 2?" step3__point--done":""))} onMouseOver={activate(2)}>
            2
        </div>
        <div className={"step3__point step3__point--3" + (active === 3?" step3__point--active":(active > 3?" step3__point--done":""))} onMouseOver={activate(3)}>
            3
        </div>
        <div className={"step3__point step3__point--4" + (active === 4?" step3__point--active":(active > 4?" step3__point--done":""))} onMouseOver={activate(4)}>
            4
        </div>
        <div className={"step3__point step3__point--5" + (active === 5?" step3__point--active":"")} onMouseOver={activate(5)}>
            5
        </div>
    </div>
}