import {T, useTNavigate} from "../translation";
import React, {useState} from "react";
import "./Step5.css";

export default function Step5() {
    const [active, setActive] = useState(1)
    const navigate = useTNavigate()

    const activate = function (n: number) {
        return () => {
            if (n !== active) {
                return
            }
            if (n === 5) {
                navigate("")
                return
            }
            setActive(n+1)
        }
    }

    return <div className={"step5"}>
        <div className={"boxcenter"} style={{marginTop: "1.5rem"}}><div className={"box"}>
            <div className={"text text--center large"}>
                <T en={"Click each red point!"} de={"Klicke jeden roten Punkt an!"} />
            </div>
        </div></div>
        <div className={"step5__point step5__point--1" + (active === 1?" step5__point--active":(active > 1?" step5__point--done":""))} onClick={activate(1)}>
            1
        </div>
        <div className={"step5__point step5__point--2" + (active === 2?" step5__point--active":(active > 2?" step5__point--done":""))} onClick={activate(2)}>
            2
        </div>
        <div className={"step5__point step5__point--3" + (active === 3?" step5__point--active":(active > 3?" step5__point--done":""))} onClick={activate(3)}>
            3
        </div>
        <div className={"step5__point step5__point--4" + (active === 4?" step5__point--active":(active > 4?" step5__point--done":""))} onClick={activate(4)}>
            4
        </div>
        <div className={"step5__point step5__point--5" + (active === 5?" step5__point--active":"")} onClick={activate(5)}>
            5
        </div>
    </div>
}