import React from 'react';
import './ErrorPage.css';
import {T} from "./translation";
import {isRouteErrorResponse, useRouteError} from "react-router-dom";
import Navigation from "./Navigation";

function ErrorPage() {

    const error = useRouteError();

    if ((isRouteErrorResponse(error) && error.status === 404) || typeof error === "undefined") {
        return (
            <>
                <Navigation />
                <div className={"box"}>
                    <div className={"text"}>
                        <T de={
                            <>
                                Die gewählte Seite wurde nicht gefunden. Bitte drücke die Escape <kbd>ESC</kbd> Taste auf deiner Tastatur um auf die Startseite zurückzukehren.
                            </>
                        } en={
                            <>
                            The selected page was not found. Please press the Escape <kbd>ESC</kbd> key on your keyboard to return to the start page.
                            </>
                        }/>
                    </div>
                </div>
            </>
        );
    }

    return (
        <>
            <Navigation />
            <div className={"box"}>
                <div className={"text"}>
                    <T de={
                        <>
                            Es ist ein Fehler passiert. Bitte drücke die Escape <kbd>ESC</kbd> Taste auf deiner Tastatur um auf die Startseite zurückzukehren.
                        </>
                    } en={
                        <>
                            An error happened. Please press the Escape <kbd>ESC</kbd> key on your keyboard to return to the start page.
                        </>
                    }/>
                </div>
            </div>
        </>
    );
}

export default ErrorPage;
