import {T, TLink} from "../translation";
import Touchpad from "./step2/touchpad.svg";
import Mouse from "./step2/mouse.svg";
import "./Step2.css";

interface Step2Props {
    mouseType: number
}

function Step2Touchpad() {
    return <>
        <div className={"text"}>
            <T
                en={<ul>
                    <li>Place one finger gently on the touchpad to operate it.</li>
                    <li>Move your finger to the <strong>left or right</strong> to move the cursor on the screen to the left or to the right.</li>
                    <li>Move your finger <strong>forward and backward</strong> to move the cursor on the screen <strong>up or down</strong>.</li>
                    <li>Press <kbd>&rarr;</kbd> on your keyboard to continue.</li>
            </ul>}
                de={<ul>
                    <li>Lege einen Finger sanft auf das Touchpad.</li>
                    <li>Bewege den Finger nach <strong>links oder rechts</strong> um den Mauszeiger auf dem Bildschirm nach links oder rechts zu bewegen.</li>
                    <li>Bewege den Finger <strong>forwärts oder rückwärts</strong> um den Mauszeiger <strong>rauf oder runter</strong> zu bewegen.</li>
                    <li>Drücke <kbd>&rarr;</kbd> auf der Tastatur für den nächsten Schritt.</li>
                </ul>}
            />
        </div>
        <img src={Touchpad} alt={""} />
    </>
}

function Step2Mouse() {
    return <>
        <div className={"text"}>
            <T
                en={<ul>
                    <li>Place on the mouse to operate it.</li>
                    <li>Move the mouse to the <strong>left or right on the table</strong> to move the cursor on the screen to the left or to the right.</li>
                    <li>Move the mouse <strong>forwards and backwards on the table</strong> to move the cursor on the screen <strong>up or down</strong>.</li>
                    <li>Press <TLink to={"/mouse/3"}><kbd>&rarr;</kbd></TLink> on your keyboard to continue.</li>
                </ul>}
                de={<ul>
                    <li>Lege deine Hand auf die Maus.</li>
                    <li>Bewege die Maus nach <strong>links oder rechts auf dem Tisch</strong> um den Mauszeiger auf dem Bildschirm nach links oder rechts zu bewegen.</li>
                    <li>Bewege sie Maus <strong>forwärts oder rückwärts auf dem Tisch</strong> um den Mauszeiger <strong>rauf oder runter</strong> zu bewegen.</li>
                    <li>Drücke <TLink to={"/mouse/3"}><kbd>&rarr;</kbd></TLink> auf der Tastatur für den nächsten Schritt.</li>
                </ul>}
            />
        </div>
        <img src={Mouse} alt={""} />
    </>
}

export default function Step2(props: Step2Props) {
    return <div className={"boxcenter step2"}><div className={"box box--flex"}>
        {
            props.mouseType === 1?<Step2Mouse />:<Step2Touchpad />
        }
    </div></div>
}