import {useParams} from "react-router-dom";
import Navigation from "../Navigation";
import "./index.css";
import {Dispatch, SetStateAction, useState} from "react";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step5 from "./Step5";
import Step4 from "./Step4";

interface MousePagePathParams {
    step?: string
}

interface MousePageSwitchProps {
    step: number
    mouseType: number
    setMouseType: Dispatch<SetStateAction<number>>
}

function MousePageSwitch(props: MousePageSwitchProps) {
    switch (props.step) {
        case 1:
            return <Step1 setMouseType={props.setMouseType} />
        case 2:
            return <Step2 mouseType={props.mouseType} />
        case 3:
            return <Step3 />
        case 4:
            return <Step4 mouseType={props.mouseType} />
        case 5:
            return <Step5 />
        default:
            throw new Response("Not Found", { status: 404 });
    }
}

export default function MousePage() {
    const [mouseType, setMouseType] = useState(1)

    let params = useParams() as Readonly<MousePagePathParams>;
    let step = 1
    const maxSteps = 5
    if (typeof params === "undefined" || params.step == null) {
        throw new Response("Invalid step specified", { status: 500 })
    }
    step = parseInt(params.step)
    return <>
        <Navigation nav={{lesson:"mouse",step:step,maxSteps:maxSteps}} />
        <MousePageSwitch step={step} mouseType={mouseType} setMouseType={setMouseType} />
    </>
}

