import {T, TLink} from "../translation";
import Touchpad from "./step4/touchpad-click.svg";
import Mouse from "./step4/mouse-click.svg";
import {ReactComponent as MouseTester} from "./step4/mouse-tester.svg";
import {ReactComponent as TouchpadTester} from "./step4/touchpad-tester.svg";
import React, {useRef, useState} from "react";
import "./Step4.css"

interface Step4Props {
    mouseType: number
}

function Step4Touchpad() {
    return <>
        <div className={"text"}>
            <h2><T en={"Clicking with the touchpad"} de={<>Mit dem Touchpad &bdquo;klicken&rdquo;</>} /></h2>
            <T
                en={<ul>
                    <li>Sometimes, you need to &ldquo;click&rdquo; something.</li>
                    <li>Use your finger to press the left button on your touchpad.</li>
                    <li>If your touchpad doesn't have a visible button, gently tap it for a click.</li>
                    <li>Try it out: move your mouse cursor on the touchpad symbol to the right and click the button!</li>
                </ul>}
                de={<ul>
                    <li>Manchmal musst du etwas &bdquo;anklicken&rdquo;.</li>
                    <li>Drücke mit deinem Zeigefinger den Knopf auf deinem Touchpad.</li>
                    <li>Wenn dein Touchpad keinen sichbaren Knopf hat, tippe es mit der Fingerspitze kurz an um zu klicken.</li>
                    <li>Versuche es: bewege deinen Mauszeiger auf das Maussymbol rechts und klicke es an!</li>
                </ul>}
            />
        </div>
        <img src={Touchpad} alt={""} />
    </>
}

function Step4Mouse() {
    return <>
        <div className={"text"}>
            <h2><T en={"Clicking with the mouse"} de={<>Mit der Maus &bdquo;klicken&rdquo;</>} /></h2>
            <T
                en={<ul>
                    <li>Sometimes, you need to &ldquo;click&rdquo; something.</li>
                    <li>Use your finger to press the button on your mouse.</li>
                    <li>If it has multiple buttons, press the <em>left most large button</em>.</li>
                </ul>}
                de={<ul>
                    <li>Manchmal musst du etwas &bdquo;anklicken&rdquo;.</li>
                    <li>Drücke mit deinem Zeigefinger den Knopf auf deiner Maus.</li>
                    <li>Wenn deine Maus mehrere Knöpfe hat,verwende den <em>linken großen Knopf</em>.</li>
                </ul>}
            />
        </div>
        <img src={Mouse} alt={""} />
    </>
}
export default function Step4(props: Step4Props) {
    const [clicked, setClicked] = useState(false)
    const imageRef = useRef<SVGSVGElement>(null)

    const onMouseDown = function (e: React.MouseEvent<HTMLDivElement>) {
        if (imageRef.current === null) {
            return
        }
        try {
            switch (e.button) {
                case 0:
                    imageRef.current.getElementById("LMB").setAttribute("class","active")
                    setClicked(true)
                    e.preventDefault()
                    break
                case 1:
                    imageRef.current.getElementById("MMB").setAttribute("class","active")
                    e.preventDefault()
                    break
                case 2:
                    imageRef.current.getElementById("RMB").setAttribute("class","active")
                    e.preventDefault()
                    break
            }
        } catch (e) {

        }
    }

    const onMouseUp = function (e: React.MouseEvent<HTMLDivElement>) {
        if (imageRef.current === null) {
            return
        }
        try {
            switch (e.button) {
                case 0:
                    imageRef.current.getElementById("LMB").setAttribute("class","")
                    e.preventDefault()
                    break
                case 1:
                    imageRef.current.getElementById("MMB").setAttribute("class","")
                    e.preventDefault()
                    break
                case 2:
                    imageRef.current.getElementById("RMB").setAttribute("class","")
                    e.preventDefault()
                    break
            }
        } catch (e) {

        }
    }

    return <div className={"boxgrid step4"}>
        <div className={"box box--flex"}>
            {
                props.mouseType === 1?<Step4Mouse />:<Step4Touchpad />
            }
        </div>
        <div className={"box box--flex"}>
            <h2><T en={"Try it out!"} de={"Versuche es!"} /></h2>
            <p><T en={"Move your mouse cursor over the symbol below and click it with the left mouse button!"} de={"Bewege deinen Mauszeiger auf dieses Symbol und klicke es mit der linken Maustaste an!"} /></p>
            <div className={"imagecontainer"} onMouseDown={onMouseDown} onMouseUp={onMouseUp} onContextMenu={(e) => {e.preventDefault()}}>
            {
                props.mouseType === 1?<MouseTester ref={imageRef} />:<TouchpadTester ref={imageRef} />
            }
            </div>
            {clicked?<p className={"large mt"}><T en={<>Very good! <TLink to={"/mouse/5"}>Click here to continue.</TLink></>} de={<>Sehr gut! <TLink to={"/mouse/5"}>Klicke hier für den nächsten Schritt.</TLink></>} /></p>:null}
        </div>
    </div>
}