import {T, useTNavigate} from "../translation";
import Mouse from "./step1/mouse-static.svg";
import Touchpad from "./step1/touchpad-static.svg";
import React, {Dispatch, SetStateAction, useCallback} from "react";

interface Step1Props {
    setMouseType: Dispatch<SetStateAction<number>>
}

export default function Step1(props: Step1Props) {
    const tNavigate = useTNavigate()

    const setMouse = useCallback(() => {
        props.setMouseType(1)
        tNavigate("/mouse/2")
    }, [tNavigate, props])

    const setTouchpad = useCallback(() => {
        props.setMouseType(2)
        tNavigate("/mouse/2")
    }, [tNavigate, props])

    const handleKeyDown = useCallback((e: KeyboardEvent) => {
        switch (e.key) {
            case "1":
                setMouse()
                break
            case "2":
                setTouchpad()
                break
        }
    }, [setMouse, setTouchpad])


    React.useEffect(() => {
        document.addEventListener('keydown', handleKeyDown)

        return () => {
            document.removeEventListener('keydown', handleKeyDown)
        }
    })

    return <>
        <div className={"box"}>
            <div className={"text"}>
                <h1><T en={"Please select your mouse"} de={"Bitte wähle deine Maus aus"}/></h1>
                <p><T
                    en={<>Press the number <kbd>1</kbd> on your keyboard if you have a mouse, or number <kbd>2</kbd> if you have a
                        touchpad.</>}
                    de={<>Drücke die Zahl <kbd>1</kbd> auf deiner Tastatur wenn Du eine Maus hast, oder die Zahl <kbd>2</kbd> wenn Du ein
                        Touchpad hast.</>}
                /></p>
            </div>
        </div>

        <div className={"mouse-container"}>
            <div className={"box"} onClick={() => {
                setMouse()
            }}>
                <h2><T en={<><kbd>1</kbd> Mouse</>} de={<><kbd>1</kbd> Maus</>}/></h2>
                <img src={Mouse} alt={"A classic mouse"} height={512} width={512} />
            </div>
            <div className={"box"} onClick={() => {
                setTouchpad()
            }}>
                <h2><T en={<><kbd>2</kbd> Touchpad</>} de={<><kbd>2</kbd> Touchpad</>}/></h2>
                <img src={Touchpad} className={"full"} alt={"A touchpad on a laptop"} height={512} width={512} />
            </div>
        </div>
    </>
}